import { useStyle, DocStyle } from '@ali/4ever-cangjie';
import { SzUtil } from '@ali/4ever-utils';

export function getDocDefaultSz(style?: DocStyle) {
  let defaultSz: number | undefined;
  const { rPr } = style?.docDefaults?.data || {};
  if (rPr?.sz) {
    defaultSz = SzUtil.toPt(rPr.sz, rPr.szUnit || 'px');
  }
  return defaultSz;
}

export default function useDefaultSz() {
  const docStyle = useStyle();
  return getDocDefaultSz(docStyle);
}
